<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12" v-if="!connected">
          <v-form ref="form" v-model="valid">
            <h1>Prihlásenie</h1>
            <v-text-field
              v-model="roomName"
              :rules="[rules.required, rules.room]"
              label="Názov zboru"
              hint="Slúži na identifikáciu zboru"
              filled
              rounded
            ></v-text-field>
            <v-text-field
              filled
              rounded
              v-model="password"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required, rules.min]"
              :type="show1 ? 'text' : 'password'"
              label="Prístupové heslo zboru"
              hint="Slúži na vstup (pripojenie) do zboru"
              @click:append="show1 = !show1"
            ></v-text-field>
            <v-text-field
              filled
              rounded
              v-model="pin"
              :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="[rules.required]"
              :type="show2 ? 'text' : 'password'"
              inputmode="numeric"
              label="PIN"
              hint="Slúži na vyhlásenie poplachu na diaľku"
              @click:append="show2 = !show2"
            ></v-text-field>
            <v-btn outlined :disabled="!valid" @click="connect()">Pripojiť</v-btn>
          </v-form>
        </v-col>

        <v-col cols="12" v-if="connected">
          <v-toolbar-title style="margin-bottom: 16px">
            <v-icon x-small color="success">mdi-checkbox-blank-circle</v-icon>
            <b style="margin-left: 6px">{{roomName ? roomName : "ws1.test"}}</b>
            <v-btn text @click="changeCredentials();" style="margin-left: 8px">Upraviť údaje</v-btn>
          </v-toolbar-title>
          <v-card>
            <v-card-title class="headline" style="font-weight: 500;">
              Vyhlásenie poplachu
              <br />na diaľku
            </v-card-title>
            <v-card-text>
              Všetci pripojení členovia budú ihneď upozornení. Systém upozorní aj členov ktorý sa pripoja po vyhlásení poplachu.
              <br />Nižšie môžete pridať krátku správu.
            </v-card-text>
            <v-card-text>
              <v-textarea v-model="customMessage" outlined label="Správa"></v-textarea>
            </v-card-text>
            <v-card-actions>
              <v-progress-linear rounded color="error" :value="count"></v-progress-linear>
              <v-spacer></v-spacer>
              <v-btn
                style="margin-left: 8px"
                color="error"
                text
                @mousedown="start"
                @mouseleave="stop"
                @mouseup="stop"
                @touchstart="start"
                @touchend="stop"
                @touchcancel="stop"
              >Vyhlásiť</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-snackbar v-model="snackbar" :timeout="timeout" multi-line top>
      {{ snackbarText }}
      <template v-slot:action>
        <v-btn text @click="snackbar = false">Ok</v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: "Login",
  data: () => ({
    valid: false,
    roomName: localStorage.getItem("roomName") || "",
    password: localStorage.getItem("password") || "",
    pin: localStorage.getItem("pin") || "",
    show1: false,
    show2: false,
    rules: {
      required: (v) => !!v || "Povinné",
      min: (v) => v.length >= 8 || "Minimálne 8 znakov",
      room: (v) => /^\S*$/.test(v) || "Nesmmie obsahovať medzery",
    },
    customMessage: "",
    socket: null,
    connected: false,
    interval: false,
    count: 0,
    snackbar: false,
    snackbarText: "",
    timeout: -1,
  }),
  created: function () {
    this.connect();
  },
  methods: {
    connect: function () {
      if (this.roomName && this.password && this.pin) {
        localStorage.setItem("roomName", this.roomName);
        localStorage.setItem("password", this.password);
        localStorage.setItem("pin", this.pin);

        console.log("connecting");
        const wsServer = this.roomName.match(/[^.]*/)[0];
        this.socket = new WebSocket(`wss://api.${wsServer}.hasime.sk`);

        this.socket.onopen = () => {
          this.socket.send(
            JSON.stringify({
              action: "join",
              name: `ovládač_${Math.floor(Math.random() * 101)}`,
              room: this.roomName,
              password: this.password,
              pin: this.pin,
              token: localStorage.getItem("token"),
            })
          );
          this.connected = true;
        };

        this.socket.onmessage = (message) => {
          const json = JSON.parse(message.data);
          switch (json.action) {
            case "token": {
              localStorage.setItem("token", json.token);
              break;
            }
            case "alert": {
              if (json.message === this.customMessage) {
                this.snackbarText =
                  "Poplach bol úspešne vyhlásený. Odpájam sa...";
                setTimeout(() => {
                  this.socket.close();
                }, 5000);
                this.snackbar = true;
                this.customMessage = "";
              }
              break;
            }
            case "ping": {
              this.socket.send(JSON.stringify({ action: "pong" }));
              break;
            }
          }
        };

        this.socket.onclose = () => {
          this.connected = false;
          console.log("Socket is closed.");
          // setTimeout(() => {
          //   console.log("Trying to reconnect...");
          //   this.socket.close();
          //   this.connect();
          // }, 5000);
        };

        this.socket.onerror = () => {
          this.connected = false;
          this.socket.close();
          setTimeout(() => {
            console.log("Trying to reconnect...");
            this.socket.close();
            this.connect();
          }, 5000);
        };
      }
    },
    start() {
      if (!this.interval) {
        this.interval = setInterval(() => {
          this.count += 10;
          if (this.count === 110) {
            console.log("confirmed");
            this.remoteAlert();
            this.count = 0;
          }
        }, 250);
      }
    },
    stop() {
      clearInterval(this.interval);
      this.interval = false;
      this.count = 0;
    },
    remoteAlert: function () {
      if (this.connected)
        this.socket.send(
          JSON.stringify({
            action: "remoteAlert",
            message: `${this.customMessage}`,
            pin: this.pin,
          })
        );
    },
    changeCredentials() {
      this.socket.close();
      localStorage.removeItem("token");
    },
  },
};
</script>

<style>
h1 {
  margin-bottom: 24px;
}
</style>
